* {
  font-family: Arial, Helvetica, sans-serif;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 7px;
}

.progress {
  height: 20px;
  width: 500px;
  background-color: rgb(233, 236, 239);
  border: 1px solid black;
  border-radius: 15px;
  overflow: hidden;
  position: relative;
}

.progress span {
  position: absolute;
  left: 0;
  display: flex;
  justify-content: center;
  width: 100%;
  align-items: center;
  z-index: 2;
}

.progress div {
  height: 100%;
  background-color: #00c251;
}